import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs'; // rx 5.5+ way

import { Log } from '../models/Log';

@Injectable({
  providedIn: 'root'
})
export class LogService {
  logs: Log[];

  private logSource = new BehaviorSubject<Log>({id: null, text: null, date: null});
  selectedLog = this.logSource.asObservable();

  private stateSource = new BehaviorSubject<boolean>(true);
  stateClear = this.stateSource.asObservable();

  constructor() {
/*     this.logs = [
      {id: '1', text: 'Generated components', date: new Date('12/26/2017 12:54:23')},
      {id: '2', text: 'Added Bootstrap', date: new Date('12/27/2017 9:33:13')},
      {id: '3', text: 'Added logs component', date: new Date('12/27/2017 12:00:13')}
    ]; */

    this.logs = [];
  }

  getLogs(): Observable<Log[]> {
    // this implements local storage
    if (localStorage.getItem('logs') === null) {
      this.logs = [];
    } else {
      this.logs = JSON.parse(localStorage.getItem('logs'));
    }
    return of(this.logs.sort((a, b) => {
      return b.date = a.date;
    }));
  }

  setFormLog(log: Log) {
    this.logSource.next(log);
  }

  addLog(log: Log) {
    this.logs.unshift(log); // put at beginning of array

    // add to local storage (needs to be a string)
    localStorage.setItem('logs', JSON.stringify(this.logs));
  }

  updateLog(log: Log) {
    // find and remove log
    this.logs.forEach((cur, index) => {
      if (log.id === cur.id) {
        this.logs.splice(index, 1); // removes log
      }
    });

    this.logs.unshift(log); // add updated log to beginning of array

    // update local storage
     // add to local storage (needs to be a string)
     localStorage.setItem('logs', JSON.stringify(this.logs));
  }

  deleteLog(log: Log) {
    // find and remove log
    this.logs.forEach((cur, index) => {
      if (log.id === cur.id) {
        this.logs.splice(index, 1); // removes log
      }
    });

     // delete from local storage
     localStorage.setItem('logs', JSON.stringify(this.logs));
  }

  clearState() {
    this.stateSource.next(true);
  }
}
